import React from 'react';
import {
    Box,
    Button,
    Card,
    CardActions,
    CardContent,
    Container,
    List,
    ListItem,
    ListItemText,
    Typography,
    Divider
} from '@mui/material';
import Grid from '@mui/material/Grid';
import { Link as RouterLink } from "react-router-dom";
import VerticalLinearStepper from "../../VerticalLinearStepper";

const objectives = [
    {
        id: 'lecture1',
        text: 'Lecture 1: Functions',
        subObjectives: [
            { id: 'lecture1Content', text: 'Read Lecture 1', route: '/week2/lecture1' },
            { id: 'lecture1Questionnaire', text: 'Complete Questionnaire', route: '/week2/lecture1/questionnaire' },
        ],
    },
    {
        id: 'lecture2',
        text: 'Lecture 2: File I/O',
        subObjectives: [
            { id: 'lecture2Content', text: 'Read Lecture 2', route: '/week2/lecture2' },
            { id: 'lecture2Questionnaire', text: 'Complete Questionnaire', route: '/week2/lecture2/questionnaire' },
        ],
    },
    {
        id: 'lecture3',
        text: 'Lecture 3: Advanced Data Operations',
        subObjectives: [
            { id: 'lecture3Content', text: 'Read Lecture 3', route: '/week2/lecture3' },
            { id: 'lecture3Questionnaire', text: 'Complete Questionnaire', route: '/week2/lecture3/questionnaire' },
        ],
    },
    {
        id: 'lecture4',
        text: 'Lecture 4: Clean Code for Functions, File I/O, and Data Operations',
        subObjectives: [
            { id: 'lecture4Content', text: 'Read Lecture 4', route: '/week2/lecture4' },
            { id: 'lecture4Questionnaire', text: 'Complete Questionnaire', route: '/week2/lecture4/questionnaire' },
        ],
    },
    {
        id: 'assignment1',
        text: 'Week 2 Assignment',
        subObjectives: [
            { id: 'assignment1Instructions', text: 'Read Assignment Instructions', route: '/week2/assignment/instructions' },
        ],
    },
];

const Week2: React.FC = () => {
    return (
        <Container maxWidth="md">
            <Box my={4}>
                <Typography variant="h3" component="h1" gutterBottom>
                    Week 2: Functions, File I/O, and Advanced Data Operations
                </Typography>
                <Box sx={{ position: 'fixed', top: '250px', right: '20px', width: '300px', height: '90vh', overflowX: 'hidden' }}>
                    <VerticalLinearStepper objectives={objectives} />
                </Box>
                <Grid container my={4}>
                    <Grid xs={8}>
                        <Typography variant="h4" component="h2" gutterBottom>
                            Objectives
                        </Typography>
                        <Typography variant="h6" component="h3" gutterBottom>
                            Learning Objectives
                        </Typography>
                        <List>
                            <ListItem>
                                <ListItemText primary="Define and use functions"/>
                            </ListItem>
                            <ListItem>
                                <ListItemText primary="File input/output operations"/>
                            </ListItem>
                            <ListItem>
                                <ListItemText primary="List comprehensions and lambda functions"/>
                            </ListItem>
                            <ListItem>
                                <ListItemText primary="Best coding practices and clean code for functions, file input/output, and list comprehension"/>
                            </ListItem>
                        </List>
                        <Typography variant="h6" component="h3" gutterBottom>
                            Coding Objectives
                        </Typography>
                        <List>
                            <ListItem>
                                <ListItemText primary="Save and load planet data to/from files"/>
                            </ListItem>
                            <ListItem>
                                <ListItemText primary="Implement advanced data operations for planet attributes"/>
                            </ListItem>
                        </List>
                        <Typography variant="h6" component="h3" gutterBottom>
                            Activities
                        </Typography>
                        <Typography variant="body1" paragraph>
                            <strong>Lecture:</strong> Functions, file I/O, advanced data operations, and clean code practices.
                        </Typography>
                        <Typography variant="body1" paragraph>
                            <strong>Coding Exercise:</strong> Write functions to save and load planet data to/from a file.
                        </Typography>
                        <Typography variant="body1" paragraph>
                            <strong>Assignment:</strong> Enhance the planet generation to include additional attributes (e.g., type of planet: rocky, gas giant).
                        </Typography>
                    </Grid>
                </Grid>
                <Box my={4}>
                    <Divider />
                    <Typography variant="h4" component="h2" gutterBottom my={2}>
                        Lectures
                    </Typography>
                    <Grid container spacing={4}>
                        <Grid item xs={12} md={4}>
                            <Card style={{"height": "250px"}}>
                                <CardContent>
                                    <Typography variant="h5" component="h3">
                                        Lecture 1: Functions
                                    </Typography>
                                    <Typography variant="body2" component="p">
                                        Learn how to define and use functions in Python.
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button size="small" component={RouterLink} to="/week2/lecture1">
                                        Go to Lecture
                                    </Button>
                                </CardActions>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card style={{"height": "250px"}}>
                                <CardContent>
                                    <Typography variant="h5" component="h3">
                                        Lecture 2: File I/O
                                    </Typography>
                                    <Typography variant="body2" component="p">
                                        Understand file input/output operations in Python.
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button size="small" component={RouterLink} to="/week2/lecture2">
                                        Go to Lecture
                                    </Button>
                                </CardActions>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card style={{"height": "250px"}}>
                                <CardContent>
                                    <Typography variant="h5" component="h3">
                                        Lecture 3: Advanced Data Operations
                                    </Typography>
                                    <Typography variant="body2" component="p">
                                        Learn advanced data operations such as list comprehensions and lambda functions.
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button size="small" component={RouterLink} to="/week2/lecture3">
                                        Go to Lecture
                                    </Button>
                                </CardActions>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card style={{"height": "250px"}}>
                                <CardContent>
                                    <Typography variant="h5" component="h3">
                                        Lecture 4: Clean Code for Functions, File I/O, and Data Operations
                                    </Typography>
                                    <Typography variant="body2" component="p">
                                        Learn best practices for writing clean and maintainable code.
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button size="small" component={RouterLink} to="/week2/lecture4">
                                        Go to Lecture
                                    </Button>
                                </CardActions>
                            </Card>
                        </Grid>
                    </Grid>
                </Box>
                <Divider />
                <Box my={4}>
                    <Typography variant="h4" component="h2" gutterBottom my={2}>
                        Assignments
                    </Typography>
                    <Button variant="contained" color="primary" component={RouterLink} to="/week2/assignment/instructions">
                        View Assignment Instructions
                    </Button>
                </Box>
            </Box>
        </Container>
    );
};

export default Week2;
